import React, { Fragment } from 'react'

import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { useAlert } from 'react-alert'
import Search from './Search'
import { logout } from '../../actions/userActions'

const Header = () => {

    const alert = useAlert();
    const dispatch = useDispatch();

    const { user, loading } = useSelector(state => state.auth)
    const { cartItems } = useSelector(state => state.cart)

    const logoutHandler = () => {
        dispatch(logout());
        alert.success('Logged out successfully')
    }

    return (
        <Fragment>
            <nav className="navbar row">
                <div className="col-12 col-md-3">
                    <div className="navbar-brand">
                        <Link to="/">
                            <img src="/images/logo2.png" alt='Olive-zone' />
                        </Link>
                    </div>
                </div>

                <div className="col-12 col-md-6 mt-2 mt-md-0">
                    {/* <Routes>
                        <Route render={(history) => <Search history={window.history} />} />
                    </Routes> */}
                    <Search />

                </div>

                <div className="col-12 col-md-3 mt-4 mt-md-0 text-center">

                    <Link to="/cart" style={{ textDecoration: 'none' }} >
                        <span id="cart" className="ms-3">Cart</span>
                        <span className="ms-1" id="cart_count">{cartItems.length}</span>
                    </Link>
                    {user ? (
                        <div className="ms-4 dropdown d-inline">

                            <Link to="#" className="btn dropdown-toggle text-white" type="button" id="dropDownMenuButton" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false" >

                                <figure className="avatar avatar-nav">
                                    <img src={user.avatar && user.avatar.url} alt={user && user.name} className="rounded-circle" />

                                </figure>
                                <span>{user && user.name}</span>

                            </Link>

                            <div className="dropdown-menu" aria-labelledby="dropDownMenuButton">

                                {user && user.role !== 'admin' ? (
                                   null
                                ) : (
                                    <Link className="dropdown-item" to="/dashboard">
                                        Dashboard
                                    </Link>
                                )}
                                 <Link className="dropdown-item" to="/orders/me">
                                        Orders
                                    </Link>
                                <Link className="dropdown-item" to="/me">
                                    Profile
                                </Link>
                                <Link className="dropdown-item text-danger" to="/" onClick={logoutHandler}>
                                    Logout
                                </Link>

                            </div>

                        </div>
                    ) : !loading && <Link to="/login" className="btn ms-4" id="login_btn">Login</Link>}

                </div>
            </nav>
        </Fragment>
    )
}

export default Header
